import React from 'react'
import hobbits from "../images/race_hobbit.jpg"

export default() =>
<div>
    <div className="container">
        <div className="row">
            <div className="col-lg-12">
                <img className="race-hobbit" src={hobbits} />
                <h1>Hobbits</h1>
                <p><strong>Hobbits</strong>, also know as Halflings, are an ancient mortal race that live in Middle-Earth. Although their exact origins are unknown, they were initially found in the northern regions of Middle-Earth and below the Vales of Anduin. At the beginning of the Third Age, Hobbits moved north and west. Most of their race eventually founded the land of the Shire in about the year TA 1601, though one type of Hobbit known as Stoors remained in the Anduin Value.</p>
                <h2>Attributes</h2>
                <table className="table">
                    <thead>
                        <tr>
                            <th>Strength</th>
                            <th>Intelligence</th>
                            <th>Will</th>
                            <th>Dexterity</th>
                            <th>Constitution</th>
                            <th>Learning Ability</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>Very Low</td>
                            <td>Low</td>
                            <td>Normal</td>
                            <td>High</td>
                            <td>High</td>
                            <td>Normal</td>
                        </tr>
                    </tbody>
                </table>
                <div className="row">
                    <div className="col-lg-12">
                        
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>


